@use "base/mixins" as m;
@use "base/variables" as v;

.modal {
    position: absolute;
    background-color: rgb(0 0 0 / 0.8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .formulario {
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        width: 90%;
        margin: 0 auto;
        transform: translateY(-30rem);
        transition-property: all;
        transition-duration: 500ms;
        transition-timing-function: ease-in-out;
        opacity: 0;
        border-radius: 6px;
        @include m.tablet {
            width: 100%;
        }

        legend {
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
        }

        input[type="text"] {
            border: 1px solid v.$gris;
            border-radius: 1.5rem;
        }
        .opciones {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 1rem;

            @include m.telefono {
                flex-direction: row;
            }
        }
        .submit-nueva-tarea {
            @include m.boton(v.$indigo, v.$blanco);
            border-radius: 4px;
        }

        .cerrar-modal {
            @include m.boton(v.$naranja, v.$blanco);
            border-radius: 4px;
        }
        &.animar {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        &.cerrar {
            transform: translateY(-200%);
            opacity: 0;
        }
    }
}
.swal2-html-container {
    color: white;
    text-align: center;
}
