@use "base/mixins" as m;
@use "base/variables" as v;
.acciones {
    margin-top: 3rem;
    @include m.tablet {
        display: flex;
        justify-content: space-between;
    }

    a {
        display: block;
        text-align: center;
        margin-bottom: 1rem;
        color: v.$negro;
        &:last-of-type {
            margin-bottom: 0;
        }
    }
}
