@use "base/mixins" as m;
@use "base/variables" as v;

.login {
    margin-top: 2rem;
    .uptask {
        @include m.textoDegradaddo(v.$cyan);
    }

    input[type="submit"] {
        @include m.boton(v.$cyan, v.$blanco);
    }

    .acciones {
        a {
            color: v.$cyan;
        }
    }
}
